<template>
    <div id="popup">
        <p class="word">
            Lorem ipsum dolor sit, amet consectetur adipisicing elit. Illum soluta blanditiis assumenda sapiente nemo ipsam quaerat, unde nisi accusamus pariatur esse cumque similique aperiam magnam nesciunt excepturi suscipit in id.
        </p>
        <form action="#">
            <label for="key">搜索</label><input id="key" type="text" v-model.lazy.trim="keyValue">
            <button type="button" @click="isIndexDate=!isIndexDate">选择日期</button>
            <button type="button" @click="isIndexTag=!isIndexTag">选择标签</button>
            <div v-show="isIndexDate">
                <label for="start">开始日期</label><input type="date" id="start" v-model="dateStart" placeholder="dateStart">
                <label for="end">结束日期</label><input type="date" id="end" v-model="dateEnd">
            </div>  
            <div v-show="isIndexTag">
                <label for="tagselect">标签</label><select name="" id="tagselect" v-model="selectTag">
                    <option value="">请选择一个标签，进行筛选</option>
                    <option v-for="(item,index) of clipTags" :key="index" v-bind:value="item">{{item}}</option>
                </select>
            </div>
        </form>
        <ul>
            <li class="itemshow" v-for="(item,index) in showBoard" :key="item.id">
                <span>{{index}}</span><span>{{item.content}}</span><span>{{item.tag}}</span>
                <button type="button" id="delete" @click="deleteItem($event,item)">删除</button>
            </li>
        </ul>
    </div>            
</template>

<script>
    export default {
        name: "PopUp",
        data(){
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth()>8?date.getMonth()+1:"0"+(date.getMonth()+1);
            let day = date.getDate()>9?date.getDate():"0"+date.getDate();
            let dateToday = year + "-" + month + "-" + day; 
            let clipBoard = chrome.storage.local.get(['clipData'],function(result){return result.clipData});  
            let clipTags =  chrome.storage.local.get(['tags'],function(result){return result.clipData});    
            return{
                clipBoard,
                clipTags,
                keyValue:"",
                selectTag:"",
                dateStart:dateToday,
                dateEnd:dateToday,
                isIndexDate:false,
                isIndexTag:false
            }
        },
        computed: {
            showBoard(){
                let arr = this.clipBoard.slice(1,this.clipBoard.length).filter((item)=>{
                   return item.content.indexOf(this.keyValue) !== -1;
                })
                if(this.isIndexDate){
                    let checkTime1 = Date.parse(this.dateStart)+new Date().getTimezoneOffset()*60*1000;
                    let checkTime2 = Date.parse(this.dateEnd)+new Date().getTimezoneOffset()*60*1000+24*60*60*1000;
                    arr = arr.filter((item)=>{
                        return item.id >= checkTime1 && item.id < checkTime2;
                    })    
                }
                if(this.isIndexTag&&this.selectTag){
                    arr = arr.filter((item)=>{
                        return item.tag == this.selectTag;
                    })    
                }
                return arr;
            }
        },
        methods: {
            deleteItem(event,item){
                this.clipBoard.splice(this.clipBoard.indexOf(item),1);
                // chrome.storage.local.set({clipItems: clipBoard}, function() {
                //     console.log('Value is set to ' + clipBoard);
                // });
            }
        }
    }
</script>

